:root {
    --top: 25px;
    --buttom: 25px;
    --top-left: 50%;
    --buttom-left: 50%;
    --pading-input: 12px;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    max-width: 100vw;
    position: relative;
    overflow-x: hidden;
    color: white;
}

.navBar {
    margin-bottom: 30px;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(to right, rgb(69, 0, 96), rgb(118, 0, 182));
    color: white;
}
.navBar h2 {
    font-size: 16px;
}
.navItem h2 {
    font-size: 20px;
}
.navItem {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

/* form  */

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    max-width: 1020px;
    margin: auto;
    padding: 0 40px;
}

.input-inline,
.inline-input-nasted {
    width: 100%;
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
}

.input-inline > * {
    flex: 1;
    border: 3px solid #0000001f;
    padding: var(--pading-input);
    font-size: 16px;
    border-radius: 5px;
    color: rgb(89, 89, 89);
    max-width: 100%;
}

.input-inline > *:focus,
.two-fields input:focus {
    outline: none;
}

.submit {
    width: 100%;
}

.submit button,
.download {
    width: 100%;
    padding: 15px;
    font-weight: bold;
    background: linear-gradient(to right, rgb(89, 4, 122), rgb(161, 0, 247));
    color: white;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.meme {
    position: relative;
    min-height: 55vh;
    max-width: 1020px;
    margin: auto;
    margin-top: 40px;
    padding: 0 40px;
}

.meme img {
    object-fit: contain;
    object-position: center;
    height: 55vh;
    width: 100%;
}

.meme > div {
    text-shadow: 0px 1px 5px black, 0px -1px 5px black;
    position: absolute;
    font-size: 24px;
}

.top {
    top: var(--top);
    left: var(--top-left);
    transform: translateX(-50%);
}
.buttom {
    bottom: var(--buttom);
    left: var(--buttom-left);
    transform: translateX(-50%);
}

.notActive {
    pointer-events: none;
}

/* input */
.two-fields {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    max-width: 100%;
}
.two-fields input {
    max-width: 100%;
    flex: 1;
    padding: var(--pading-input);
    border: 3px solid #0000001f;
}

.flex {
    position: unset !important;
    display: flex;
    align-items: center;
    justify-content: center;
}

.download {
    width: 50%;
    margin: 22px 0;
}

@media (max-width: 650px) {
    .input-inline {
        flex-direction: column;
    }
    .meme {
        min-height: 0;
    }
    .meme img {
        height: 100%;
    }
    .download {
        width: 100%;
    }
}
